#app-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    height: 100vh;
}

#wrapper p{
    text-align: center;
    font-size: 1.5rem;
    color: #000;
    text-decoration: underline;
}

#form-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 80vh;
    width: 40vw;
    overflow-y: auto;
    box-shadow: 10px 10px #000;
    background-color: #fff;
    border-style: solid;
    border-color: #000;
    border-width: 2px;
}
#cv-form{
    width: 95%;
    background-color: #fff;
    color: #000;
}

fieldset{
    display: flex;
    flex-direction: column;
    margin-top: 1em;
    border: none;
}

legend{
    font-size: 1.5rem;
}

input{
    height: 2.5rem;
    border-style: solid;
    border-width: .1px;
}

textarea{
    margin-top: 1em;
    height: 5rem;
    font-size: 1rem;
}
input, #description{
    font-size: 1rem;
    margin-top: 1em;
}

input, textarea, button{
    border-style: solid;
    border-color: #000;
}

button{
    height: fit-content;
    width: 10rem;
    font-size: 1.5rem;
    margin-top: .5em;
    background-color: #ff1f71;
    color: #000;
    border-width: 1px;
}

button:hover{
    background-color: #ff015e;;
    box-shadow: 5px 5px #000;
}

.section-header{
    text-decoration: underline;
    text-decoration-color: gold;
    text-decoration-thickness: 5px;
}

#description{
    height: 5rem;
}

.form-list{
    list-style: none;
    padding: 0;
}

.add-button, .remove-button{
    font-size: 2rem;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin: 0;
    padding: 0;
}

h1{
    margin-left: .5em;
}

ul{
    list-style: none;
    padding: 0;
}

#list-button, .remove-button{
    margin: .5em;
}

#language-and-skill-list{
    width: 100%;
    display: grid;
    grid-template-columns: 3fr 1fr;
}

@media screen and (max-width:768px){
    #app-container{
        flex-direction: column;
        align-items: center;
        height: fit-content;
    }

    #form-container{
        width: 90vw;
        justify-self: center;
        overflow: unset;
        height: fit-content;
    }
}